.top-bar {
  padding-block: 24px;

  @include content-padding-in-line;

  display: flex;
  justify-content: space-between;
  align-items: center;

  @include on-tablet {
    padding-block: 64px;
  }

  &__icons {
    display: flex;
    gap: 32px;
  }

  &__logo-link {
    display: flex;
  }

  &__logo {
    height: 38px;

    @include hover(transform, scale(1.2));

    &--primary-logo {
      @include on-tablet {
        height: 56px;
      }

      @include hover(transform, scale(1.2));
    }
  }
}
